import React from 'react';
import { css } from '@emotion/css';
import Tooltip from '@material-ui/core/Tooltip';

import styles from './styles';

const TimelineCard = ({ boss }) => {

    const tooltipText = () => (
        <React.Fragment>
            <p className={css(styles.timelineCardTooltip)}>{boss.name}</p>
        </React.Fragment>
    );

    return (
        <div className={css(styles.timelineCardContainer)}>
            <Tooltip title={tooltipText()} placement={'top'} arrow>
                <img 
                    src={boss.icon} 
                    alt={boss.name} 
                    width={40} 
                    height={40}
                    className={css(styles.bossImage)}
                />
            </Tooltip>
            <p className={css(styles.timelineCardSubtext)}>{boss.count}</p>
        </div>
    );
};

export default TimelineCard;